  isCollider = (gameObj) => {
    let objChildren = gameObj.children;

    if(objChildren.length > 1){
      objChildren.forEach(function(childObj) {
        this.sceneObjects.canCollide.push(childObj)
      }, this);
    } else {
      this.sceneObjects.canCollide.push(gameObj)
    }

  }

